import React, { useState } from 'react'
// import { useParams } from 'react-router-dom'
import { Container } from '@material-ui/core'
import AppHeader from '../../components/Header/AppHeader'
import PostHeader from './components/PostHeader'
import PostDetails from './components/PostDetails'
import PostComments from './components/PostComments'

export default props => {
  // const params = useParams()
  // const { id } = params // Recipe ID (The UUID was returned from API)

  const [recipe] = useState({
    title: 'Salad nhit i',
    thumbnail:
      'https://realfood.tesco.com/media/images/Ritas-enchiladas-recipe-1400x919-1c7ff22b-ea5e-44cf-9ada-d7b04420002f-0-1400x919.jpg',
    tags: ['Cng thc', 'Salad', 'Salad nhit i'],
    owner: {
      name: 'Matilda Brown',
      avatar:
        'https://www.takadada.com/wp-content/uploads/2019/07/avatar-anime-nu-cho-facebook-73.jpg'
    },
    readyTime: 10, // mins
    cookTime: 40, // mins
    ration: 4, // mins
    rating: 30, // person
    materials: [
      {
        name: 'C ng',
        gram: 200
      },
      {
        name: 'X lch',
        gram: 300
      },
      {
        name: 'Bp ci tm',
        gram: 200
      },
      {
        name: 'C chua bi',
        gram: 200
      },
      {
        name: 'Gia v',
        gram: 0
      }
    ],
    steps: [
      {
        name: 'S ch nguyn liu',
        todo: [
          'Ra sch x lch, ci tm, c chua bi...',
          'X lch ct khc ngn.',
          'Ci tm ct si.'
        ]
      },
      {
        name: 'Ch bin',
        todo: [
          'Lm st trn: Cho 50gr dm, 30gr du oliu, 20gr mayonaise, 5gr mui, 25gr ng, 2gr tiu ri nh tan u, thm tng t nu thch v cay (c th b vo my xay).'
        ]
      }
    ],
    comments: [
      {
        username: 'stevenblack1717',
        avatar:
          'https://www.takadada.com/wp-content/uploads/2019/07/avatar-anime-nu-cho-facebook-73.jpg',
        text: 'Chu mi nga'
      }
    ]
  })

  return (
    <>
      <AppHeader />
      <Container maxWidth="md">
        <PostHeader
          tags={recipe.tags}
          thumbnail={recipe.thumbnail}
          title={recipe.title}
          owner={recipe.owner}
        />
        <PostDetails
          readyTime={recipe.readyTime}
          cookTime={recipe.cookTime}
          ration={recipe.ration}
          rating={recipe.rating}
          materials={recipe.materials}
          steps={recipe.steps}
        />
        <hr style={{ margin: '2rem 0' }} />
        <PostComments owner={recipe.owner} comments={recipe.comments} />
      </Container>
    </>
  )
}
