import { Form, Formik } from "formik";
import React, { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import { COLOR } from "ultis/functions";
import * as yup from "yup";
import "./signin.css";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import { useDispatch, useSelector } from "react-redux";
import { SignInRequest } from "./redux/actions";

function SignIn() {
  const history = useHistory();
  const dispatch = useDispatch();
  const user = useSelector((state) => state.Auth?.user);

  useEffect(() => {
    if (user) {
      history.push("/");
    }
  }, []);

  const validationSchema = yup.object().shape({
    password: yup
      .string()
      .required("* Vui lng nhp mt khu")
      .matches(/(?=.{8,})/, {
        message: "Mt khu phi gm 8 k t",
      }),
    username: yup.string().trim().required("* Vui lng nhp tn ng nhp"),
  });

  const handleLogin = (values) => {
    dispatch(SignInRequest.get(values));
  };

  const handleKeyPress = (isValid, event, values) => {
    if (isValid && event.key === "Enter") {
      handleLogin(values);
    }
  };

  return (
    <div id="bg">
      <div id="loginBg">
        <span className="dimoName">Lemon-aid</span>
        <div id="loginBox">
          <span id="loginStyle">ng nhp</span>
          <Formik
            initialValues={{
              password: "",
              username: "",
            }}
            isInitialValid={false}
            validationSchema={validationSchema}
            onSubmit={(values) => handleLogin(values)}
          >
            {({
              handleChange,
              handleBlur,
              handleSubmit,
              values,
              isValid,
              errors,
              touched,
              setFieldTouched,
            }) => {
              return (
                <Form className="formStyle">
                  <TextField
                    id={
                      errors.username
                        ? "outlined-error-helper-text"
                        : "outlined-required"
                    }
                    helperText={errors.username && errors.username}
                    label="Tn ng nhp"
                    variant="outlined"
                    value={values.username}
                    onChange={handleChange("username")}
                    onTouchStart={() => setFieldTouched("username")}
                    onBlur={handleBlur("username")}
                    onKeyPress={(event) =>
                      handleKeyPress(isValid, event, values)
                    }
                  />
                  <TextField
                    id={
                      errors.password
                        ? "outlined-error-helper-text"
                        : "outlined-password-input"
                    }
                    helperText={errors.password && errors.password}
                    label="Mt khu"
                    variant="outlined"
                    onChange={handleChange("password")}
                    onTouchStart={() => setFieldTouched("password")}
                    value={values.password}
                    onBlur={handleBlur("password")}
                    onKeyPress={(event) =>
                      handleKeyPress(isValid, event, values)
                    }
                  />
                  <div className="buttomBox">
                    <Button onClick={() => history.push("/forgot")}>
                      Qun mt khu
                    </Button>
                    <div>
                      <span>Cha c ti khon?</span>
                      <Button onClick={() => history.push("/signup")}>
                        ng k
                      </Button>
                    </div>
                  </div>
                  <Button
                    id="loginBtn"
                    disabled={!isValid}
                    style={{
                      backgroundColor: isValid
                        ? COLOR.primary
                        : COLOR.deactiveGray,
                    }}
                    onClick={handleSubmit}
                    size={"large"}
                  >
                    ng nhp
                  </Button>
                </Form>
              );
            }}
          </Formik>
          <span style={{ fontSize: 18, marginTop: 16, marginBottom: 16 }}>
            ng nhp bng
          </span>
          <Button size="small">
            <img
              src={require("assets/Google.png")}
              style={{ width: "40%" }}
              alt="gg"
            />
          </Button>
        </div>
      </div>
      <div id="imgBg">
        <img src={require("../../assets/signin_img.svg")} alt="img" />
        <span className="tagline">Vo bp khng kh</span>
        <span className="tagline">C Lemon-aid lo</span>
      </div>
    </div>
  );
}

export default SignIn;
